@use '../../abstracts' as *;

#hero {
    @media #{$break1}, #{$break2} {
        margin-block-end: 10rem;
    }
    @media #{$break3open} {
        margin-block-end: 12rem;
    }
}

#hero-title {
    font-family: $ff-secondary;
}

.l-hero {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4.5rem;

    @media #{$break1}, #{$break2} {
        flex-direction: column-reverse;
        gap: 3rem;
    }

    @media #{$break3open} {
        gap: 4.5rem;
        
        &::before {
            --p-background-color: hsl(var(--clr-neutral-700), 12%);

            content: '';
            position: absolute;
            top: -10.5rem;
            right: calc(-1 * (100vw - 100%)/2 - 10.25rem);
            height: pxToRem(703.5);
            inline-size: calc((100vw - 100%)/2);
            background-color: var(--p-background-color);
            z-index: -1;

            #{$theme-dark} & {
                --p-background-color: hsl(var(--clr-primary-400));
            }
        }
    }


}

.hero-bg {
    position: absolute;
    z-index: -1;

    @media #{$break1}, #{$break2} {
        display: block;
        top: -9.5rem;
        inline-size: 100vw;
        block-size: 400px;
    }
    @media #{$break3open} {
        right: -10.25rem;
        top: -10.5rem;
    }

    & > svg {
        --p-svg-color: hsl(var(--clr-neutral-700), 12%);

        color: var(--p-svg-color);

        #{$theme-dark} & {
            --p-svg-color: hsl(var(--clr-primary-400));
        }
    }
}

.hero-graphic {
    position: relative;
    max-inline-size: pxToRem(400);
}

.hero-graphic-tag {
    --p-text-color: hsl(var(--clr-primary-400), 65);
    --p-border-color: var(--p-text-color);
    --p-background-color: hsl(var(--clr-neutral-200));

    display: inline-block;
    font-size: $fs-4;
    line-height: 1.33333;
    padding: 0.375rem 0.65rem;
    color: var(--p-text-color);
    background-color: var(--p-background-color);
    border-radius: 100vh;
    position: absolute;
    border: 0.165rem solid var(--p-border-color);

    &:nth-of-type(1) {
        bottom: 0;
        left: 2.5rem;
    }
    &:nth-of-type(2) {
        top: 1.3rem;
        right: 0;
    }

    #{$theme-dark} & {
        --p-text-color: hsl(var(--clr-neutral-200));
        --p-border-color: hsl(var(--clr-neutral-200), 30%);
        --p-background-color: hsl(var(--clr-neutral-600));
    }
}

.hero-img {
    --p-border-color: hsl(var(--clr-neutral-100));

    display: block;
    inline-size: 100%;
    block-size: 100%;
    object-fit: cover;
    aspect-ratio: 1 / 1;
    border: 12px solid var(--p-border-color);
    border-radius: 100vh;
    overflow: hidden;

    #{$theme-dark} & {
        --p-border-color: hsl(var(--clr-neutral-600));
    }
}

.hero-text {
    max-width: pxToRem(700);
    @media #{$break1}, #{$break2} {
        text-align: center;
    }

    .btn-primary,
    .btn-secondary {
        @media #{$break1} {
            inline-size: 100%;
      }
    }

    .btn-secondary {
        margin-right: 1rem;

        @media #{$break1} {
            margin-block-end: 1rem;
        }
    }

}

.hero-body {
    padding-block-start: 1.25rem;
    @media #{$break1}, #{$break2} {
        padding-block-end: 2.5rem;
    }
    @media #{$break3open} {
        padding-block-end: 3.5rem;
    }
    
}




